/**
 * @file lib/services/agent-service.ts
 * @description Êô∫ËÉΩ‰Ωì‰∏öÂä°ÈÄªËæëÊúçÂä°ÔºåÊèê‰æõCRUDÊìç‰Ωú„ÄÅÊêúÁ¥¢Á≠õÈÄâ„ÄÅÊùÉÈôêÊéßÂà∂Á≠âÂäüËÉΩ
 * @author zk-agentÂºÄÂèëÂõ¢Èòü
 * @lastUpdate 2024-12-19
 * @updateLog
 *   - 2024-12-19 ÂÆåÊàêÊô∫ËÉΩ‰ΩìÊúçÂä°Âü∫Á°ÄÂÆûÁé∞
 *   - 2024-12-19 Ê∑ªÂä†ÂëΩÂêçËßÑËåÉÂ§áÊ≥®ÂíåÊñπÊ≥ïËØ¥Êòé
 * 
 * üî§ ÂëΩÂêçËßÑËåÉËØ¥ÊòéÔºö
 * - ÊúçÂä°Á±ªÔºöServiceÂêéÁºÄÔºàÂ¶ÇÔºöAgentServiceÔºâ
 * - CRUDÊñπÊ≥ïÔºöcreate/get/update/delete + ÂÆû‰ΩìÂêçÔºàÂ¶ÇÔºöcreateAgentÔºâ
 * - Êü•ËØ¢ÊñπÊ≥ïÔºöfind/list + Êù°‰ª∂ÔºàÂ¶ÇÔºöfindAgentById, listAgentsÔºâ
 * - È™åËØÅÊñπÊ≥ïÔºövalidate + ÂØπË±°ÔºàÂ¶ÇÔºövalidateAgentDataÔºâ
 * - ÊùÉÈôêÊñπÊ≥ïÔºöcheck + ÊùÉÈôêÁ±ªÂûãÔºàÂ¶ÇÔºöcheckOwnershipÔºâ
 * - ÂèÇÊï∞ÂØπË±°ÔºöÊé•Âè£Âêç + ParamsÔºàÂ¶ÇÔºöListAgentsParamsÔºâ
 */

import { prisma } from '@/lib/database';
import { PrismaClient, Prisma } from '@prisma/client'
import type { Agent } from '@prisma/client'
import { 
  Agent, 
  AgentQueryParams, 
  CreateAgentRequest, 
  UpdateAgentRequest,
  AgentType,
  AgentStatus 
} from '@/types/agents';
import { PaginationParams } from '@/types/core';
import { 
  IAgentService, 
  CreateAgentData, 
  UpdateAgentData, 
  ListAgentsParams,
  AgentError,
  AgentValidationError,
  AgentNotFoundError
} from '../interfaces/agent-manager.interface'
import { injectable } from '../di/container'

// üìù ÂëΩÂêçËßÑËåÉÔºöÂèÇÊï∞Êé•Âè£‰ΩøÁî®ÊèèËø∞ÊÄßÂêçÁß∞ + ParamsÂêéÁºÄ
export interface ListAgentsParams extends PaginationParams, AgentQueryParams {
  type?: AgentType
  status?: AgentStatus
  tags?: string[]
  sortBy?: 'createdAt' | 'updatedAt' | 'name' | 'rating' | 'usageCount'
  sortOrder?: 'asc' | 'desc'
  visibility?: 'public' | 'private' | 'all'
}

// üìù ÂëΩÂêçËßÑËåÉÔºöÂàõÂª∫Êï∞ÊçÆÊé•Âè£‰ΩøÁî®Create + ÂÆû‰ΩìÂêç + Data
export interface CreateAgentData {
  name: string
  description: string
  type: AgentType
  capabilities: string[]
  configuration: Record<string, any>
  tags?: string[]
  visibility?: 'public' | 'private'
  isActive?: boolean
}

// üìù ÂëΩÂêçËßÑËåÉÔºöÊõ¥Êñ∞Êï∞ÊçÆÊé•Âè£‰ΩøÁî®Update + ÂÆû‰ΩìÂêç + Data
export interface UpdateAgentData {
  name?: string
  description?: string
  capabilities?: string[]
  configuration?: Record<string, any>
  tags?: string[]
  visibility?: 'public' | 'private'
  isActive?: boolean
  status?: AgentStatus
}

// üìù ÂëΩÂêçËßÑËåÉÔºöÊúçÂä°Á±ª‰ΩøÁî®PascalCaseÔºåServiceÂêéÁºÄÊòéÁ°ÆË°®Á§∫‰∏öÂä°ÈÄªËæëÂ±Ç
@injectable
export class AgentService implements IAgentService {
  // Ëé∑ÂèñÊô∫ËÉΩ‰ΩìÂàóË°®ÔºàÊîØÊåÅÂàÜÈ°µ„ÄÅÊêúÁ¥¢„ÄÅÁ≠õÈÄâÔºâ
  static async getAgents(
    queryParams: ListAgentsParams = {}
  ) {
    const {
      page = 1,
      limit = 10,
      sortBy = 'createdAt',
      sortOrder = 'desc',
      type,
      status,
      tags,
      search,
      isPublic,
      ownerId
    } = queryParams;

    // ËæìÂÖ•È™åËØÅ
    if (page < 1) {
      throw new AgentValidationError('È°µÁ†ÅÂøÖÈ°ªÂ§ß‰∫é0', ['invalid page number'])
    }
    if (limit < 1 || limit > 100) {
      throw new AgentValidationError('ÊØèÈ°µÊï∞ÈáèÂøÖÈ°ªÂú®1-100‰πãÈó¥', ['invalid page size'])
    }

    const skip: any = (page - 1) * limit;
    
    // ÊûÑÂª∫Êü•ËØ¢Êù°‰ª∂
    const where: any = {};
    
    if (type) where.type = type;
    if (status) where.status = status;
    if (isPublic !== undefined) where.isPublic = isPublic;
    if (ownerId) where.ownerId = ownerId;
    
    // Ê†áÁ≠æÁ≠õÈÄâ
    if (tags && tags.length > 0) {
      where.tags = {
        hasSome: tags
      };
    }
    
    // ÊêúÁ¥¢ÂäüËÉΩ
    if (search) {
      where.OR = [
        { name: { contains: search, mode: 'insensitive' } },
        { description: { contains: search, mode: 'insensitive' } },
        { capabilities: { hasSome: [search] } }
      ];
    }

    // ÊâßË°åÊü•ËØ¢
    const [agents, total] = await Promise.all([
      prisma.agentConfig.findMany({
        where,
        skip,
        take: limit,
        orderBy: { [sortBy]: sortOrder },
        include: {
          _count: {
            select: {
              sessions: true,
              messages: true
            }
          }
        }
      }),
      prisma.agentConfig.count({ where })
    ]);

    return {
      agents,
      pagination: {
        page,
        limit,
        total,
        totalPages: Math.ceil(total / limit)
      }
    };
  }

  // Ê†πÊçÆIDËé∑ÂèñÂçï‰∏™Êô∫ËÉΩ‰Ωì
  async getAgentById(id: string): Promise<Agent | null> {
    if (!id?.trim()) {
      throw new AgentValidationError('Êô∫ËÉΩ‰ΩìID‰∏çËÉΩ‰∏∫Á©∫', ['id is required'])
    }

    try {
      const agent: any = await prisma.agentConfig.findUnique({
        where: { id },
        include: {
          _count: {
            select: {
              sessions: true,
              messages: true
            }
          }
        }
      });

      if (!agent) return null;

      // ËΩ¨Êç¢‰∏∫ÂâçÁ´ØÁ±ªÂûãÊ†ºÂºè
      return this.transformPrismaToAgent(agent);
    } catch (error) {
      console.error('Ëé∑ÂèñÊô∫ËÉΩ‰ΩìËØ¶ÊÉÖÂ§±Ë¥•:', error)
      throw new AgentError('Ëé∑ÂèñÊô∫ËÉΩ‰ΩìËØ¶ÊÉÖÂ§±Ë¥•', 'GET_FAILED', { originalError: error })
    }
  }

  // ÂàõÂª∫Êñ∞Êô∫ËÉΩ‰Ωì
  static async createAgent(
    data: CreateAgentData,
    ownerId?: string
  ): Promise<Agent> {
    // ËæìÂÖ•È™åËØÅ
    if (!data.name?.trim()) {
      throw new AgentValidationError('Êô∫ËÉΩ‰ΩìÂêçÁß∞‰∏çËÉΩ‰∏∫Á©∫', ['name is required'])
    }
    if (!data.description?.trim()) {
      throw new AgentValidationError('Êô∫ËÉΩ‰ΩìÊèèËø∞‰∏çËÉΩ‰∏∫Á©∫', ['description is required'])
    }
    if (ownerId && !ownerId.trim()) {
      throw new AgentValidationError('ÊâÄÊúâËÄÖID‰∏çËÉΩ‰∏∫Á©∫', ['ownerId is required'])
    }

    // È™åËØÅÁ±ªÂûãÊûö‰∏æ
    const validTypes = ['CONVERSATION', 'CAD_ANALYZER', 'POSTER_GENERATOR'] as const
    if (data.type && !validTypes.includes(data.type)) {
      throw new AgentValidationError(`Êó†ÊïàÁöÑÊô∫ËÉΩ‰ΩìÁ±ªÂûã: ${data.type}`, ['invalid agent type'])
    }

    try {
      const agent: any = await prisma.agentConfig.create({
        data: {
          name: data.name.trim(),
          description: data.description.trim(),
          type: data.type,
          status: AgentStatus.ACTIVE,
          avatar: data.avatar?.trim() || null,
          tags: Array.isArray(data.tags) ? data.tags.filter(tag => tag?.trim()) : [],
          apiEndpoint: data.apiEndpoint?.trim() || null,
          capabilities: Array.isArray(data.capabilities) ? data.capabilities.filter(cap => cap?.trim()) : [],
          configuration: typeof data.configuration === 'object' && data.configuration !== null ? data.configuration : {},
          version: '1.0.0',
          isPublic: data.isPublic ?? true,
          ownerId,
          metrics: {
            totalRequests: 0,
            successfulRequests: 0,
            failedRequests: 0,
            averageResponseTime: 0,
            dailyActiveUsers: 0,
            weeklyActiveUsers: 0,
            monthlyActiveUsers: 0,
            rating: 0,
            reviewCount: 0,
            uptime: 0
          }
        },
        include: {
          _count: {
            select: {
              sessions: true,
              messages: true
            }
          }
        }
      });

      return this.transformPrismaToAgent(agent);
    } catch (error) {
      if (error instanceof Prisma.PrismaClientKnownRequestError) {
        if (error.code === 'P2002') {
          throw new AgentError('Êô∫ËÉΩ‰ΩìÂêçÁß∞Â∑≤Â≠òÂú®', 'DUPLICATE_NAME')
        }
        if (error.code === 'P2003') {
          throw new AgentError('ÊâÄÊúâËÄÖ‰∏çÂ≠òÂú®', 'OWNER_NOT_FOUND')
        }
      }
      console.error('ÂàõÂª∫Êô∫ËÉΩ‰ΩìÂ§±Ë¥•:', error)
      throw new AgentError('ÂàõÂª∫Êô∫ËÉΩ‰ΩìÂ§±Ë¥•', 'CREATE_FAILED', { originalError: error })
    }
  }

  // Êõ¥Êñ∞Êô∫ËÉΩ‰Ωì
  async updateAgent(
    id: string,
    data: UpdateAgentData,
    ownerId?: string
  ): Promise<Agent | null> {
    if (!id?.trim()) {
      throw new AgentValidationError('Êô∫ËÉΩ‰ΩìID‰∏çËÉΩ‰∏∫Á©∫', ['id is required'])
    }

    // È™åËØÅÊõ¥Êñ∞Êï∞ÊçÆ
    if (data.name !== undefined && !data.name.trim()) {
      throw new AgentValidationError('Êô∫ËÉΩ‰ΩìÂêçÁß∞‰∏çËÉΩ‰∏∫Á©∫', ['name cannot be empty'])
    }
    if (data.description !== undefined && !data.description.trim()) {
      throw new AgentValidationError('Êô∫ËÉΩ‰ΩìÊèèËø∞‰∏çËÉΩ‰∏∫Á©∫', ['description cannot be empty'])
    }

    // È™åËØÅÊùÉÈôêÔºàÂ¶ÇÊûúÊúâownerIdÔºåÁ°Æ‰øùÂè™ËÉΩÊõ¥Êñ∞Ëá™Â∑±ÁöÑagentÔºâ
    if (ownerId) {
      const existingAgent: any = await prisma.agentConfig.findUnique({
        where: { id },
        select: { ownerId: true }
      });

      if (!existingAgent) {
        throw new AgentNotFoundError(id)
      }
      if (existingAgent.ownerId !== ownerId) {
        throw new AgentError('Êó†ÊùÉÈôêÊõ¥Êñ∞Ê≠§Êô∫ËÉΩ‰Ωì', 'UNAUTHORIZED')
      }
    }

    try {
      const updateData: any = {};
      
      if (data.name) updateData.name = data.name.trim();
      if (data.description) updateData.description = data.description.trim();
      if (data.type) updateData.type = data.type;
      if (data.status) updateData.status = data.status;
      if (data.tags) updateData.tags = data.tags.filter(tag => tag?.trim());
      if (data.capabilities) updateData.capabilities = data.capabilities.filter(cap => cap?.trim());
      if (data.configuration) updateData.configuration = data.configuration;
      if (data.visibility) updateData.isPublic = data.visibility === 'public';
      if (data.isActive !== undefined) updateData.isActive = data.isActive;

      const agent: any = await prisma.agentConfig.update({
        where: { id },
        data: updateData,
        include: {
          _count: {
            select: {
              sessions: true,
              messages: true
            }
          }
        }
      });

      return this.transformPrismaToAgent(agent);
    } catch (error) {
      if (error instanceof Prisma.PrismaClientKnownRequestError) {
        if (error.code === 'P2002') {
          throw new AgentError('Êô∫ËÉΩ‰ΩìÂêçÁß∞Â∑≤Â≠òÂú®', 'DUPLICATE_NAME')
        }
        if (error.code === 'P2025') {
          throw new AgentNotFoundError(id)
        }
      }
      console.error('Êõ¥Êñ∞Êô∫ËÉΩ‰ΩìÂ§±Ë¥•:', error)
      throw new AgentError('Êõ¥Êñ∞Êô∫ËÉΩ‰ΩìÂ§±Ë¥•', 'UPDATE_FAILED', { originalError: error })
    }
  }

  // Âà†Èô§Êô∫ËÉΩ‰Ωì
  async deleteAgent(id: string, ownerId?: string): Promise<boolean> {
    if (!id?.trim()) {
      throw new AgentValidationError('Êô∫ËÉΩ‰ΩìID‰∏çËÉΩ‰∏∫Á©∫', ['id is required'])
    }

    // È™åËØÅÊùÉÈôê
    if (ownerId) {
      const existingAgent: any = await prisma.agentConfig.findUnique({
        where: { id },
        select: { ownerId: true }
      });

      if (!existingAgent) {
        throw new AgentNotFoundError(id)
      }
      if (existingAgent.ownerId !== ownerId) {
        throw new AgentError('Êó†ÊùÉÈôêÂà†Èô§Ê≠§Êô∫ËÉΩ‰Ωì', 'UNAUTHORIZED')
      }
    }

    try {
      await prisma.agentConfig.delete({
        where: { id }
      });
      return true;
    } catch (error) {
      if (error instanceof Prisma.PrismaClientKnownRequestError) {
        if (error.code === 'P2025') {
          throw new AgentNotFoundError(id)
        }
      }
      console.error('Âà†Èô§Êô∫ËÉΩ‰ΩìÂ§±Ë¥•:', error)
      throw new AgentError('Âà†Èô§Êô∫ËÉΩ‰ΩìÂ§±Ë¥•', 'DELETE_FAILED', { originalError: error })
    }
  }

  // Ëé∑ÂèñÊé®ËçêÊô∫ËÉΩ‰Ωì
  static async getRecommendedAgents(limit: number = 6): Promise<Agent[]> {
    const agents: any = await prisma.agentConfig.findMany({
      where: {
        isPublic: true,
        status: AgentStatus.ACTIVE
      },
      orderBy: [
        { metrics: { path: ['rating'], order: 'desc' } },
        { metrics: { path: ['totalRequests'], order: 'desc' } }
      ],
      take: limit,
      include: {
        _count: {
          select: {
            sessions: true,
            messages: true
          }
        }
      }
    });

    return agents.map(this.transformPrismaToAgent);
  }

  // Ëé∑ÂèñÁÉ≠Èó®Ê†áÁ≠æ
  static async getPopularTags(limit: number = 20): Promise<string[]> {
    const agents: any = await prisma.agentConfig.findMany({
      where: {
        isPublic: true,
        status: AgentStatus.ACTIVE
      },
      select: {
        tags: true
      }
    });

    // ÁªüËÆ°Ê†áÁ≠æÈ¢ëÊ¨°
    const tagCount: Record<string, number> = {};
    agents.forEach(agent => {
      agent.tags.forEach(tag => {
        tagCount[tag] = (tagCount[tag] || 0) + 1;
      });
    });

    // ÊåâÈ¢ëÊ¨°ÊéíÂ∫èÂπ∂ËøîÂõûÂâçN‰∏™
    return Object.entries(tagCount)
      .sort(([, a], [, b]) => b - a)
      .slice(0, limit)
      .map(([tag]) => tag);
  }

  // Êõ¥Êñ∞Êô∫ËÉΩ‰ΩìÊåáÊ†á
  static async updateAgentMetrics(
    agentId: string,
    metrics: Partial<any>
  ): Promise<void> {
    await prisma.agentConfig.update({
      where: { id: agentId },
      data: {
        metrics: {
          ...(await this.getAgentMetrics(agentId)),
          ...metrics
        }
      }
    });
  }

  // Ëé∑ÂèñÊô∫ËÉΩ‰ΩìÊåáÊ†á
  static async getAgentMetrics(agentId: string): Promise<any> {
    const agent: any = await prisma.agentConfig.findUnique({
      where: { id: agentId },
      select: { metrics: true }
    });

    return agent?.metrics || {};
  }

  // Êï∞ÊçÆËΩ¨Êç¢ÔºöPrismaÊ®°Âûã -> ÂâçÁ´ØÁ±ªÂûã
  private static transformPrismaToAgent(prismaAgent: any): Agent {
    return {
      id: prismaAgent.id,
      name: prismaAgent.name,
      description: prismaAgent.description,
      type: prismaAgent.type as AgentType,
      status: prismaAgent.status as AgentStatus,
      avatar: prismaAgent.avatar,
      tags: prismaAgent.tags,
      apiEndpoint: prismaAgent.apiEndpoint,
      capabilities: prismaAgent.capabilities,
      configuration: prismaAgent.configuration,
      metrics: prismaAgent.metrics,
      version: prismaAgent.version,
      isPublic: prismaAgent.isPublic,
      ownerId: prismaAgent.ownerId,
      createdAt: prismaAgent.createdAt.toISOString(),
      updatedAt: prismaAgent.updatedAt.toISOString()
    };
  }
}